import { createApp } from "vue";
import App from "./App.vue";
import store from "./store";
import router from "./router";
import "@/assets/css/styles.css";
import VueHead from "vue-head";
const auto = () => import("es6-promise/auto");

createApp(App)
  .use(router)
  .use(store)
  .use(auto)
  .use(VueHead)
  .mount("#app");
